body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.message {
  margin: 5px 0;
  align-self: flex-start;
  background-color: #5bf5bf;
  border-radius: 16px;
  color: #ffffff;
  font-family: Arial, Helvetice, sans-serif;
  padding: 5px 15px;
}

.message-like {
  width: auto;
  height: 16px;
  padding-left: 10px;
  cursor: pointer;
}

.end {
  flex: 1 1;
}

.container-message {
  display: flex;
  justify-content: flex-start;
}

.send-block {
  border: 2px solid grey;
  border-radius: 50px;
  display: flex;
  justify-content: space-around;
}

.send {
  margin: 5px;
  border-radius: 3px;
}

.chat {
  width: 250px;
  height: 450px;
  border: 1px solid #d8d8fd;
  border-radius: 4px;
  box-shadow: 0 0 9px 1px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
}
.message-list {
  flex: 10 1;
  overflow-y: scroll;
}
.messages {
  display: flex;
  flex-direction: column-reverse;
  min-height: 100%;
  padding: 0 10px;
}
.input-message {
  margin: 5px;
  border-radius: 20px;
  border: 1px solid #eee;
  font-size: 16px;
  outline: none;
  padding: 4px 10px;
}

