.message {
  margin: 5px 0;
  align-self: flex-start;
  background-color: #5bf5bf;
  border-radius: 16px;
  color: #ffffff;
  font-family: Arial, Helvetice, sans-serif;
  padding: 5px 15px;
}

.message-like {
  width: auto;
  height: 16px;
  padding-left: 10px;
  cursor: pointer;
}

.end {
  flex: 1 1;
}

.container-message {
  display: flex;
  justify-content: flex-start;
}
